import React from 'react';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';

import Main from '../layouts/Main';

const recommendations = [
  {
    name: 'Rakesh Chauhan',
    position: 'Senior SRE Engineer',
    text: 'I had the pleasure of working with Harsh at eInfochips, where he demonstrated strong technical expertise in cloud technologies within the NOC team. Harsh’s problem-solving skills, proactive approach, and team collaboration significantly contributed to the team’s success. I highly recommend him for any future roles, confident in his capabilities.',
  },
  {
    name: 'Chirag Gandhi',
    position: 'Technical Manager',
    text: 'I can say that Harsh is a person working very silently but delivering the work timely and efficiently. He is a very good team player, always ready to support teammates.',
  },
];

const Index = () => {
  const sliderSettings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  return (
    <Main
      description="I’m Harsh Bhatt, a Toronto-based cybersecurity professional specializing in cloud infrastructure, automation, and network security."
    >
      <article className="post" id="index">
        <header>
          <div className="title">
            <h2>
              <Link to="/">About this site</Link>
            </h2>
            <p>
              Take a look at my work in cloud computing, cybersecurity, and network operations,
              where I leverage my expertise to create secure and efficient solutions.
            </p>
          </div>
        </header>
        <p>
          Welcome to my website! I&apos;m glad you&apos;re here. Feel free to explore more
          about my professional journey, check out my resume, or get in touch with me.
          I&apos;m always open to new opportunities and connections.
        </p>
        <div className="recommendations" style={{ maxWidth: '1000px', margin: 'auto' }}>
          <h3>Recommendations</h3>
          <Slider {...sliderSettings}>
            {recommendations.map((rec) => (
              <div key={rec.name} className="recommendation-slide">
                <blockquote>
                  <p>&quot;{rec.text}&quot;</p>
                  <footer style={{ textAlign: 'center', fontWeight: 'bold' }}>
                    {rec.name}
                    <br />
                    - {rec.position}
                  </footer>
                </blockquote>
              </div>
            ))}
          </Slider>
        </div>
      </article>
    </Main>
  );
};

export default Index;
